a.skipnav {
	@include rem(11);
	display: block;
	padding: 5px 20px;
	color: $gray-light;
}

/* columns with no gutters */
.row.no-gutter {
	margin-right:0;
	margin-left:0;

	> [class*='col-'] {
		padding-right:0;
		padding-left:0;
	}
}

.col {
	margin-bottom: 1em;
	
	> img {
		height: auto;
		max-width: 100%;
	}
	
	@media (min-width: $screen-sm) {
		margin-bottom: 0;
	}
}

/* make images full width */
img.full {
	display: block;
	height: auto;
	width: 100%;
}

@media (min-width: $screen-md) {
	/* equal height columns using table cells */
	.col-equalize {
		display: table;
		width: 100%;

		.col-td {
			float: none;
			display: table-cell;
			overflow: hidden;
			vertical-align: middle;
		}
	}	
}

@media (min-width: $screen-md) {
	/* vertical center items using flexbox */
	.vcenter {
		@include flexbox();
		@include align-items(center);
		
		.no-flexbox & {
			display: block;
		}
	}
}